.dev-project {
  border: 1px #CCC solid;
  padding: 0;
  margin: 5px 15px 20px 15px;
  .embed-responsive {
    margin: 5px 5%;
  }
}

.dev-project-img {
  img {
    width: 100%;
    padding: 10px 40px;
  }
}

.dev-project-title {
  color: $forest;
  font-family: $headerFont;
  font-size: 1.3em;
  margin: 0;
}

.dev-project-date {
  color: #888;
  font-size: 0.8em;
  font-family: $headerFont;
  margin-left: 5px;
}

.dev-project-header {
  background-color: $cream;
  border-bottom: 1px #CCC solid;
  padding: 3px 10px;
}

.dev-project-description {
  padding: 8px;
  font-size: 0.9em;
}