.bunch-of-code {
  position: absolute;
  top: 65px;
  left: 15px;
  opacity: 0.06;
  overflow: hidden;
}

.blog-title,
.main-title {
  text-align: center;
  color: $blue;
}

.blog-date {
  text-align: center;
  font-size: 1em;
  color: #666;
}

textarea.big-form {
  height: 200px;
}

.blog-content pre {
  margin: 5px 25px;
}

.blog-index {
  text-align: center;
  list-style-type: none;
  li a {
    font-family: $headerFont;
    &:hover {
      text-decoration: none;
    }
  }
}

.blog-index-date {
  color: #888;
  font-size: 0.75em;
}